<template>
  <el-radio-group v-model="labelPosition" size="small">
  <el-radio-button label="left">左对齐</el-radio-button>
  <el-radio-button label="right">右对齐</el-radio-button>
  <el-radio-button label="top">顶部对齐</el-radio-button>
</el-radio-group>
<div style="margin: 20px;"></div>
<el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign" :rules="HoustRules" ref="House_split">
  <el-form-item prop="No" label="电话号码">
    <el-input v-model="formLabelAlign.No"></el-input>
  </el-form-item>
  <el-form-item prop="Requirement" label="需求面积">
    <el-input v-model="formLabelAlign.Requirement"></el-input>
  </el-form-item>
  <el-form-item class="btn">
          <el-button @click="submit" type="primary">提交</el-button>
        </el-form-item>
</el-form>
</template>

<script>
  export default {
    data() {
      return {
        labelPosition: 'right',
        formLabelAlign: {
          No: '',
          Requirment: ''
        },
        HoustRules:{
          Cno:[
            {
            required: true,
            message: "请输入电话号码",
            trigger: "blur",
          },
          {
            min: 11,
            max: 11,
            message: "应为11个数字",
            trigger: "blur",
          },
          ],
          
          Crequirement:[
            {
            required: true,
            message: "请输入需求面积",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    submit() {
      this.$refs.House_split.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post(
          "api/consumer/update",
          this.formLabelAlign
        );
        if (res.code == 2000) {
          this.$message.error("此面积您不可租赁");
          return;
        }
        if (res.code == 2001) {
          this.$message.error("您已填写过，请等待分房");
          return;
        }
        if (res.code == 200) {
          this.$message.success("提交成功");
          return;
        }
        this.$message.error("请等待月底分房");
      });
    },
  }
}
</script>