<template>
  <el-table
    :data="tableData_"
    border
    style="width: 100%">
    <el-table-column
      fixed
      prop="Sarea"
      label="房产面积"
      width="650">
    </el-table-column>
    <el-table-column
      prop="Slow_score"
      label="阈值"
      width="645">
    </el-table-column>
  </el-table>
</template>

<script>
  export default {
      data(){
          return{
            tableData_:[],
            addForm: {
					Sarea: '',
					Slow_score: '',
				},
          }
      },
      mounted() {
    this.$http
      .get("api/admin/Score")
      .then(response => (
        this.tableData_ = response.data));
  }
    
    }

</script>