<template>
  <el-table
    :data="tableData_"
    border
    style="width: 100%">
    <el-table-column
      fixed
      prop="Sarea"
      label="房产面积"
      width="400">
    </el-table-column>
    <el-table-column
      prop="Slow_score"
      label="阈值"
      width="400">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="300">
      <template #default="scope">
        <!--修改界面-->
        <el-button @click="dialogEditOpen(scope.row.Sarea, scope.row.Slow_score),updateFormVisible = true" type="danger" icon="el-icon-edit"></el-button>
        <el-dialog  title="编辑房产信息" v-model="updateFormVisible">
        <el-form :model="Form_" ref="Score_update" :rules="controlRules">
        <el-form-item  label="房产面积" :label-width="formLabelWidth" prop="Sarea">
        <el-input :disabled="true" v-model="Form_.Sarea" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="阈值" :label-width="formLabelWidth" prop="Slow_score">
        <el-input v-model="Form_.Slow_score" autocomplete="off"></el-input>
        </el-form-item>
  </el-form>
  <template #footer>
    <span class="dialog-footer">
      <el-button @click="updateFormVisible = false">取 消</el-button>
      <el-button type="primary" @click="updateHouse(scope.$index, scope.row.Sarea),updateFormVisible = false">提交</el-button>
    </span>
  </template>
</el-dialog>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  export default {
      data(){
          return{
            tableData_:[],
            addForm: {
					Sarea: '',
					Slow_score: '',
				},
            Form_: {
					Sarea: '',
					Slow_score: '',
				},
        dialogTableVisible: false,
        dialogFormVisible: false,
        updateTableVisible: false,
        updateFormVisible: false,
        controlRules:{
          Sarea:[
            {
            required: true,
            message: "请输面积",
            trigger: "blur",
          },
          ],
          Slow_score:[
            {
            required: true,
            message: "请输入阈值",
            trigger: "blur",
          },
          ],
      },
        formLabelWidth: '120px'
          }
      },
      mounted() {
    this.$http
      .get("api/admin/Score")
      .then(response => (
        this.tableData_ = response.data));
  },
    methods: {
      handleClick(row) {
        console.log(row);
      },
     
    updateHouse(index, Sarea) {   
				this.$refs.Score_update.validate(async valid => {
					if (!valid) return;
					// 发起请求
					const {
						data: res
					} = await this.$http.put("api/admin/Score/update/"+Sarea, this.Form_);
					if (res.code == 200) {
						this.dialogEditVisible = false;
						return this.$message.success("编辑成功");
					}
					this.$message.error("编辑失败");
				})
			},
      dialogEditOpen(Sarea, Slow_score) {
				this.Form_.Sarea = String(Sarea);
				this.Form_.Slow_score = String(Slow_score);
				this.dialogEditVisible = true;
			},
    },
    
    }

</script>