<template>
<el-button type="warning" @click="dialogFormVisible = true" plain>新增房产</el-button>
<!-- <el-button type="text" @click="dialogFormVisible = true">新增房产</el-button> -->

<el-dialog title="新增房产" v-model="dialogFormVisible">
  <el-form :model="form" :rules="controlRules" ref="House_set">
    <el-form-item label="房号" :label-width="formLabelWidth" prop="Hno">
      <el-input v-model="form.Hno" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="房间面积" :label-width="formLabelWidth" prop="Harea">
      <el-input v-model="form.Harea" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="每平方米价格" :label-width="formLabelWidth" prop="Hrent_m">
      <el-input v-model="form.Hrent_m" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="是否出租" :label-width="formLabelWidth" prop="Hdistribute">
      <el-input v-model="form.Hdistribute" autocomplete="off"></el-input>
    </el-form-item>
  </el-form>
  <template #footer>
    <span class="dialog-footer">
      <el-button @click="dialogFormVisible = false">取 消</el-button>
      <el-button type="primary" @click="asubmit">提交</el-button>
    </span>
  </template>
</el-dialog>

<!--主页面-->
  <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      fixed
      prop="Hno"
      label="房号"
      width="250">
    </el-table-column>
    <el-table-column
      prop="Harea"
      label="房间面积"
      width="250">
    </el-table-column>
    <el-table-column
      prop="Hrent_m"
      label="每平方米价格"
      width="250">
    </el-table-column>
    <el-table-column
      prop="Hdistribute"
      label="是否出租"
      width="280">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="250">
      <template #default="scope">
        <!--修改界面-->
        <el-button @click="dialogEditOpen(scope.row.Hno, scope.row.Harea,  scope.row.Hrent_m, scope.row.Hdistribute),updateFormVisible = true" type="danger" icon="el-icon-edit"></el-button>
        <el-dialog  title="编辑房产信息" v-model="updateFormVisible">
        <el-form :model="Form_" ref="House_update" :rules="controlRules_">
        <el-form-item  label="房号" :label-width="formLabelWidth" prop="Hno">
        <el-input :disabled="true" v-model="Form_.Hno" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="房间面积" :label-width="formLabelWidth" prop="Harea">
        <el-input v-model="Form_.Harea" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="每平方米价格" :label-width="formLabelWidth" prop="Hrent_m">
        <el-input v-model="Form_.Hrent_m" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="是否出租" :label-width="formLabelWidth" prop="Hdistribute">
      <el-input v-model="Form_.Hdistribute" autocomplete="off"></el-input>
    </el-form-item>
  </el-form>
  <template #footer>
    <span class="dialog-footer">
      <el-button @click="updateFormVisible = false">取 消</el-button>
      <el-button type="primary" @click="updateHouse(scope.$index, scope.row.Hno),updateFormVisible = false">提交</el-button>
    </span>
  </template>
</el-dialog>
        <!-- <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button> -->
        <el-button type="primary" icon="el-icon-delete" @click="handleDelete(scope.$index, scope.row.Hno)"></el-button>
        <!-- <el-button type="text" size="small">删除</el-button> -->
      </template>
    </el-table-column>
  </el-table>
</template>


<script>
  export default {
      data(){
          return{
            tableData:[],
            addForm: {
					Hno: '',
					Harea: '',
					Hrent_m: '',
					Hdistribute: '',
				},
        dialogTableVisible: false,
        dialogFormVisible: false,
        updateTableVisible: false,
        updateFormVisible: false,
        form: {
          Hno:'',
          Harea:'',
          Hrent_m:'',
          Hdistribute:'',
        },
        Form_: {
          Hno:'',
          Harea:'',
          Hrent_m:'',
          Hdistribute:'',

        },
        controlRules:{
          Hno:[
            {
            required: true,
            message: "请输房号",
            trigger: "blur",
          },
          ],
          Harea:[
            {
            required: true,
            message: "请输入面积",
            trigger: "blur",
          },
          ],
          Hrent_m: [
            {
            required: true,
            message: "请输入每月房租",
            trigger: "blur",
          },
          ],
          Hdistribute:[
            {
            required: true,
            message: "请输入是否租赁",
            trigger: "blur",
          },
          ],
      },
      controlRules_:{
          Hno:[
            {
            required: true,
            message: "请输房号",
            trigger: "blur",
          },
          ],
          Harea:[
            {
            required: true,
            message: "请输入面积",
            trigger: "blur",
          },
          ],
          Hrent_m: [
            {
            required: true,
            message: "请输入每月房租",
            trigger: "blur",
          },
          ],
          Hdistribute:[
            {
            required: true,
            message: "请输入是否租赁",
            trigger: "blur",
          },
          ],
      },
        formLabelWidth: '120px'
          }
      },
      mounted() {
    this.$http
      .get("api/admin/control")
      .then(response => (
        this.tableData = response.data));
  },
    methods: {
      handleClick(row) {
        console.log(row);
      },
      asubmit() {
        

      this.$refs.House_set.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post(
          "api/admin/control/register",
          this.form
        );
        if (res.code == 200) {
          this.$message.success("添加成功");
          return;
        }
        this.$message.error("后台错误");
      });
    },
    handleDelete(index, Hno) {
      this.$http.delete("api/admin/control/delete/" + Hno).then(() => {
        this.tableData.splice(index, 1);
        this.$message({
          showClose: true,
          duration: 1500,
          message: "删除待办事项成功",
          type: "success"
        });
      });
    },
    updateHouse(index, Hno) {   
				this.$refs.House_update.validate(async valid => {
					if (!valid) return;
					// 发起请求
					const {
						data: res
					} = await this.$http.put("api/admin/control/update/"+Hno, this.Form_);
					if (res.code == 200) {
						this.dialogEditVisible = false;
						return this.$message.success("编辑成功");
					}
					this.$message.error("编辑失败");
				})
			},
      dialogEditOpen(Hno, Harea, Hrent_m, Hdistribute) {
				this.Form_.Hno = String(Hno);
				this.Form_.Harea = String(Harea);
				this.Form_.Hrent_m = String(Hrent_m);
				this.Form_.Hdistribute = String(Hdistribute);
				this.dialogEditVisible = true;
			},
    },
    
    }

</script>